@import "_partials/hero-animation.scss";

$accent: #46a2b8;
$text-white-50: hsla(0, 0%, 100%, 0.2);
$text-white-70: hsla(0, 0%, 100%, 0.7);
$text-white-90: hsla(0, 0%, 100%, 0.9);
$text-dark-90: rgba(0, 0, 0, 0.7);
$red: rgb(227, 7, 8);
$blue: rgb(7, 52, 156);

$white: #ffffff;
$vivid-red: #ff2d36;
$vivid-blue: #4babff;
$gold: #fdbb00;

$button-blue: #1c2749;
$button-outline-blue: #1a6591;

.test {
  margin-top: -250px;
  color: red;
  font-weight: 800;
  background: yellow;
  display: inline-block;
  padding: 1rem;
}

// ================= //
// Shared

.nav-logo {
  height: 70px;
}

.hero-wrapper {
  position: relative;
  .hero-animation {
    position: absolute;
    z-index: 10;
    bottom: 29%;
    // & > div:first-of-type {
    //   // padding-top: 30%;
    // }
    @media screen and (min-width: 655px) {
      bottom: 10%;
    }
    @media screen and (min-width: 926px) {
      // width: 70%;
      right: 10%;
      bottom: 0;
    }
    @media screen and (min-width: 1180px) {
      // width: 60%;

      width: 80%;
      bottom: -20%;
      // bottom: 0;
    }
    @media screen and (min-width: 1480px) {
      // width: 50%;
      // right: 20%;
      bottom: -5%;
    }
  }
}

.link {
  cursor: pointer;
}

.timeline-marker.white:before {
  background: $white;
}
.timeline-marker.blue:before {
  background: $vivid-blue;
}
.timeline-marker.red:before {
  background: $vivid-red;
}
.timeline-marker.gold:before {
  background: $gold;
}

.text-muted {
  font-size: 1.3rem;
  text-transform: uppercase;
  letter-spacing: 4px;
}

.particles {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

header {
  position: relative;
  min-height: 100vh;
  &::after,
  &::before {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
  }
  &:after {
    z-index: 2;
    background: linear-gradient(180deg, transparent 27%, #430000c2),
      radial-gradient(farthest-corner, transparent 0, #2b4083 100%);
  }
  .header-row {
    height: calc(100vh - 135px);
    padding-bottom: 25%;
    position: relative;
    z-index: 30 !important;
    .hitter {
      color: $text-dark-90;
      font-family: "LemonMilk";
      font-size: 0.9rem;

      color: #131313;
      // background-color: #e7e5e4;
      letter-spacing: 0.15em;
      text-shadow: 0 -3px 0 #333, 0 6px 8px rgba(0, 0, 0, 0.4),
        0 9px 10px rgba(0, 0, 0, 0.15), 0 30px 10px rgba(0, 0, 0, 0.18),
        0 15px 10px rgba(0, 0, 0, 0.21);

      .extra-blurb {
        display: none;
      }
    }
    @media screen and (min-width: 520px) {
      .hitter {
        font-size: 1.3rem;
        color: $text-white-90;
        .extra-blurb {
          text-shadow: none;
          font-family: "Montserrat-Light";
          display: block;
        }
      }
    }
    @media screen and (min-width: 580px) {
      .hitter {
        font-size: 1rem;
        color: $text-white-90;
      }
    }
    @media screen and (min-width: 890px) {
      padding-bottom: 5%;
      .blurb {
        background: #131e44b0;
        border: 1px solid #ffffff30;
        padding: 1rem;
        width: 50%;
      }
    }
    @media screen and (min-width: 929px) {
      .hitter {
        // font-size: 1.7rem;
        color: $text-white-90;
      }
    }
    @media screen and (min-width: 1100px) {
      .hitter {
        // font-size: 2rem;
        width: 50%;
        color: $text-white-90;
      }
    }
  }
}

.text-ico {
  color: $text-white-70 !important;
  &:hover {
    text-decoration: none;
    i {
      color: white;
    }
  }
}

.top-bar {
  i:before {
    font-size: 2rem;
  }
  position: relative;
  height: 55px;
  line-height: 3px;
  z-index: 210 !important;
  background-color: rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid hsla(0, 0%, 100%, 0.1);
  .wrapper-t {
    align-items: center;
  }
}

.icon-menu {
  font-size: 25px;
  line-height: 45px;
}

.small-title {
  display: inline-block;
  height: 45px;
  line-height: 45px;
  font-size: 10px;
  text-transform: uppercase;
  z-index: 1 !important;
  letter-spacing: 3px;
  color: rgba(255, 255, 255, 0.9);
}

i[class$="fill"] {
  text-align: center;
  width: 1.25em;
}

.text-white {
  b {
    color: #17a2b8;
  }
}

.text-white-50 {
  color: hsla(0, 0%, 100%, 0.5);
}

.desktop-navigation {
  height: 80px;
  a {
    position: relative;
    color: hsla(0, 0%, 100%, 0.6666666666666666);
    display: inline-block;
    min-width: 100px;
    height: 80px;
    line-height: 80px;
    font-family: "Montserrat-Light";
    transition: all 0.1s ease-in-out;
    text-decoration: none;
    &:hover:before {
      border-bottom: 3px solid #fff;
      width: 100%;
    }
    &:before {
      content: " ";
      position: absolute;
      height: 2px;
      left: 0;
      width: 0;
      border-bottom: 3px solid #fff;
      bottom: 0;
      transition: all 0.2s ease-in-out;
    }
  }
}

.mobile-navigation {
  height: 80px;
}

.hero-text {
  font-size: 3.6rem;
  font-family: "LemonMilk";
  line-height: 1em;
  font-weight: 300;
  color: hsla(0, 0%, 100%, 0.8) !important;
}

.sub-icon {
  font-size: 90px;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#f00),
    to(#333)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.sub-title {
  @media screen and (max-width: 376px) {
    font-size: 2rem;
  }
  font-size: 3rem;
  font-family: "LemonMilk";
  line-height: 1em;
  font-weight: 300;
  color: hsla(0, 0%, 100%, 0.8) !important;
}

.section-title {
  @extend .sub-title;
  font-size: 1.6rem;
  letter-spacing: 3px;
  text-align: center;
  font-family: "MontNormal";
}

.btn {
  &:not(:last-child) {
    margin-right: 0.5rem;
  }
  font-size: 0.875rem;
  font-weight: 600px !important;
  border: 1px solid transparent;
  position: relative;
  transition: all 0.15s ease;
  letter-spacing: 0.025em;
  text-transform: none;
  will-change: transform;
}

.btn-success {
  color: #fff;
  border-color: $button-outline-blue;
  background-color: $button-blue;
  &:hover {
    color: $button-blue;
    border-color: $white;
    background-color: $white;
  }
}

.btn-outline-white {
  color: $button-blue;
  border-color: #fff;
  background-color: $white;
  // background-image: none;
  &:hover {
    color: $white;
    border-color: #fff;
    background: $button-blue;
  }
}

.header-stats {
  position: relative;
  z-index: 1000;
}

/////////////////////////////
///// ADVERT
/////////////////////////////

.section-advert {
  // background: #000000ed;
  // background: white;
  z-index: 200;
  background: linear-gradient(358deg, transparent 27%, #3a0d1b),
    radial-gradient(farthest-corner, transparent 0, #020001 100%);
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
  padding: 2rem;
  .divider {
    display: none;
  }
  .token-title {
    display: flex;
    justify-content: center;
    color: $text-white-70;
    font-family: "RockSalt";
    margin-left: -20%;
    align-items: baseline;
    .available {
      // color: $red;
      color: $text-white-70;
    }
    // color: $text-white-70;
    .icon {
      position: relative;
      img {
        height: 5rem;
        // opacity: 0.5;
      }
      span {
        position: absolute;
        font-family: "RockSalt";
        color: $text-white-70;
        // color: $blue;
        top: 20%;
      }
    }
  }

  .address {
    transition: all 0.6s ease-in-out;
    font-size: 11px;
    width: 100%;
    text-align: center;
    color: $text-white-70;
    font-weight: bold;
    height: 40px;
    line-height: 40px;
    &.active {
      height: 0;
      color: $accent;
    }
  }
  .address-cpy {
    font-family: "LemonMilk";
    transition: all 0.3s ease-in-out;
    font-size: 14px;
    height: 40px;
    background: $text-white-70;
    color: rgb(16, 51, 83);
    padding: 10px;
    text-align: center;
    letter-spacing: 3px !important;
    text-transform: uppercase !important;
    cursor: pointer;
    &:hover {
      background: $text-white-90;
    }
    &.active {
      color: white;
      background-color: $accent;
    }
  }
  @media screen and (min-width: 832px) {
    // max-width: 1100px;
    font-size: 13px;
    margin: 0 auto !important;
    flex-direction: row;
    .token-title {
      .icon {
        width: 167px;
      }
    }
    .divider {
      display: inline-block;
      width: 2px;
      margin: 0 20px;
      background: $text-white-70;
    }
    .token-wrapper {
      margin-right: -10%;
      .address {
        font-size: 20px;
        border: 0;
      }
    }
  }
}

/////////////////////////////
///// ROADMAP
/////////////////////////////

.char {
  position: relative;
  z-index: 10;
  &:before {
    content: " ";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    opacity: 0.8;
    background-image: url("../assets/img/hksized.png");
    background-repeat: no-repeat;
    background-position: 0;
    @media screen and (max-width: 991px) {
      opacity: 0.5;
    }
  }
}

.section-roadmap:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 1;
  background: linear-gradient(180deg, transparent 27%, #000000),
    radial-gradient(farthest-corner, transparent 0, #3a0f1b 100%);
}

.section-roadmap {
  position: relative;
  z-index: 10;
  .timeline {
    position: relative;
    .timeline-title {
      font-family: "LemonMilk";
      font-family: "LemonMilk";
      font-size: 1.2rem;
      letter-spacing: 2px;
    }
  }
  @media (max-width: 991px) {
    .timeline {
      .text-light {
        font-weight: bold;
      }
    }
  }
}

/////////////////////////////
///// TOKENOMICS
/////////////////////////////
.section-tokenomics {
  &:after {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    opacity: 0.96;
    z-index: 1;
    background: linear-gradient(180deg, transparent 27%, #2b0404c2),
      radial-gradient(farthest-corner, #010001 0, #000000 100%);
  }
  .inner-section {
    z-index: 10;
    position: relative;
  }
  // background: #020001;
  position: relative;
  .tk-container {
    display: flex;
    flex-direction: column;
    .chart-item {
      display: flex;
      flex-direction: column;
      .bb {
        color: $text-white-70;
        text-align: center;
      }
      .btnContainer {
        display: flex;
        justify-content: center;
        .s {
          &:first-of-type {
            margin-right: 6px;
          }
          cursor: pointer;
          color: #fff;
          border-color: #fff;
          background-color: transparent;
          background-image: none;
          border: 1px solid #ffffff52;
          position: relative;
          transition: all 0.15s ease;
          letter-spacing: 0.025em;
          text-transform: none;
          will-change: transform;
          padding: 0.5rem 1rem;
          font-size: 1rem;
          line-height: 1.5;
          border-radius: 0.3rem;
          &.active {
            background-color: #f00b0b1c;
          }
        }
        // }
      }
      .list-unstyled {
        display: table;
        margin: 0 auto;
        text-align: left;
        span {
          &:first-child {
            font-weight: bold;
          }
          color: $text-white-70;
          display: inline-block;
          &.label {
            min-width: 70px;
          }
        }
      }
    }
  }
  @media screen and (min-width: 800px) {
    .chart-item {
      flex-direction: row !important;
    }
  }
  @media screen and (min-width: 990px) {
    .chart-item {
      justify-content: center;
      align-items: center;
      .text {
        width: 30%;
      }
    }
  }
}

.footer {
  position: relative;
  background: #060606;
  color: #ddd;
  > div:first-of-type {
    padding-bottom: 0;
  }
  .title {
    font-family: "LemonMilk";
    letter-spacing: 0.5px;
  }
  hr {
    &:not([size]) {
      height: 1px;
    }
    opacity: 0.25;
    color: inherit;
    background-color: currentColor;
    margin-bottom: 1rem;
    border: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
  li a {
    font-size: 0.85rem;
    display: block;
    padding: 0.125rem 0;
    color: #8898aa;
  }
}

@media screen and (max-width: 991px) {
  .desktop-navigation {
    display: none !important;
  }
}
