@font-face {
  font-family: "Montserrat-Light";
  src: local("Montserrat-Light"),
    url("./fonts/Montserrat/Montserrat-Light.ttf") format("truetype");
}

@font-face {
  font-family: "MontNormal";
  src: local("MontNormal"),
    url("./fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "RockSalt";
  src: local("RockSalt"), url("./fonts/misc/RockSalt.ttf") format("truetype");
}

@font-face {
  font-family: "Bombing";
  src: local("Bombing"), url("./fonts/misc/Bombing.ttf") format("truetype");
}

@font-face {
  font-family: "SpaceSport";
  src: local("SpaceSport"),
    url("./fonts/misc/SpaceSport.ttf") format("truetype");
}

@font-face {
  font-family: "LemonMilk";
  src: local("LemonMilk"),
    url("./fonts/misc/LEMONMILK-Bold.otf") format("opentype");
}

body {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  margin: 0;
  text-align: left;
  background-color: #f8f9fe;
  background: #1f232b;
  font-family: "Montserrat-Light", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
